    WebtipsHeader: 
    "Write your resume in the language that you would use at your workplace. This is a great chance to show the company’s representatives that you can express yourself.Tailor the CV according to job you’re applying for. There is no ‘one size fits all’ CV – so always adapt itbefore applying to a job.Write a short intro that truly represents you –not cheesy quotes, but something you truly believe in. Don’t put overused buzzwords, describe yourself and your capabilities with your own vocabulary.",
