    WebtipsEdication: 
    "Education is an important section – but be sure to select only what is relevant to the job you’re applying for.     Write about your university degrees, relevant courses and if you have any publications, but skip the high and elementary school. Don’t be afraid to include a “Informal Education” section, where you can list all the courses, podcasts webinars that you used to teach yourself about software development. Don’t include everything – just the education that is relevant and necessary for the position you are applying for.",
    WebtipsSkils: 
    "Showcase your tech stack. List the stack under every previous project you have worked on, or showcase your projects on GitHub. The easiest way to do this is to list the stack under a previous project that you have worked on. Another way is to showcase your projects on GitHub.",
