    WebtipsAchivmentsInterests:"Dates – a time frame in which you have been on a certain job title. If you are still working there, leave a hyphen ( - ). Leave your most recent experience first, and the oldest ones last. Don’t be afraid to include a “Informal Education” section, where you can list all the courses, podcasts and webinars that you used to teach yourself about software development. Don’t include everything – just the education that is relevant and necessary for the position you are applying for. Tip: Use Action verbs to demonstrate your experience. Examples include: “applied”, “improved”,“implemented” etc.",

    DigitalMarketingtipsHeader: "Write your resume in the language that you would use at your workplace. This is a great chance to show the company’s representatives that you can express yourself.Tailor the CV according to job you’re applying for. There is no ‘one size fits all’ CV – so always adapt itbefore applying to a job.Write a short intro that truly represents you –not cheesy quotes, but something you truly believe in. Don’t put overused buzzwords, describe yourself and your capabilities with your own vocabulary.",
    DigitalMarketintipsHeader:"Write your resume in the language that you would use at your workplace. This is a great chance to show the company’s representatives that you can express yourself. Put a photo that looks professional – made with a good camera and showing you in a warm light. It’s better to send a CV without a photo, than a photo that makes you seem unprofessional. Know your unique value proposition and communicate it effectively. You need a personal tagline that will help you stand out from everyone else. This line should be the first impression the hiring manager will get from you. E.g. if you’re a Content Writer, don’t just say ‘’I’m a great content writer’’. Be creative and say why they should hire you to handle their content.",
    DigitalMarketingtipsEducation:"Under education, list only the relevant studies and courses, like university degree or Digital Marketing Academy. Don’t write where you went to high school.",
    DigitalMarketingtipsWorkExpirience:"List the job experiences that are relevant to the position you’re applying for. If you’re currently working, your current job titles. Explain your responsibilities and projects and list the strategies and projects you created or worked on. Tailor your CV to the company and position you’re applying for. Highlight the keywords from the job specification on your CV. For example, if you’re applying for an e-commerce role, include keywords such as ‘’Conversion, bounce rate and Google Analytics’’.Talk about campaigns you worked on, the budget you had, how it benefited the company etc. This will help recruiters learn how you could benefit them based on your past experience.",
    DigitalMarketingtipsAchievmens:"Tip: Highlight the key achievements you have presented on your CV so the hiring manager can catch them very quickly.",
